<template>
  <v-container fluid>
    <v-row style="color:black">
      <v-col cols="3" style="background-color:"
        >
        <v-row>
          <v-col cols="12">
            <v-file-input
            color="deep-purple accent-4"
              prepend-icon="fas fa-file-upload"
              accept=".csv"
              show-size
              counter
              chips
              outlined
              dense
              @change="handleUpload"
            >
              <template v-slot:selection="{ text }">
                <v-chip small label color="primary">{{ text }}</v-chip>
              </template>
            </v-file-input>
              <p style="color:red">{{ warning.upload }}</p>
          </v-col>
        </v-row>
      </v-col>
      <v-col cols="9" style="background-color:"
        >
        <v-data-table
          v-if="tableData.length != 0"
          :headers="headers"
          :items="tableData"
          :items-per-page="10"
          class="elevation-1"
          :footer-props="{
            'items-per-page-options': [10, 20, 50, -1],
            'items-per-page-text': '顯示資料筆數:',
            'items-per-page-all-text': '全部'
          }"
          loading-text="載入資料中..."
          noDataText="沒有資料"
        ></v-data-table>
        <p v-else>無資料</p>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      types: ["會員資料", "交易資料"],
      tmpUploadData: {
        data: "",
        type: ""
      },
      warning: {
        upload: ""
      },
      snackbar: false,
      snackcolor: "green",
      text: "上傳成功",
      timeout: 2500,
      headers: [],
      tableData: [],
      loading: false
    };
  },
  watch: {
   
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    handleUpload(file) {
      if (file) {
        this.loading = true;
        this.tmpUploadData.data = file;
        this.checkFiles();
        this.tableData = [];

        if (this.warning.upload == "") {
          const reader = new FileReader();
          reader.readAsText(file);
          reader.onload = e => {
            // console.log(e.target.result);
            var data = e.target.result; //读到的数据
            let Header = [];
            let Arr = [];
            var allRows = data.split(/\r?\n|\r/);
            for (var singleRow = 0; singleRow < allRows.length; singleRow++) {
              var rowCells = allRows[singleRow].split(",");
              // console.log(rowCells);
              if (singleRow == 0) {
                Header = rowCells;
              } else {
                Arr.push(rowCells);
              }
            }
            let jsonArr = [];
            Arr.forEach(item => {
              let obj = {};
              for (let j = 0; j < Header.length; j++) {
                obj[Header[j]] = item[j];
              }
              jsonArr.push(obj);
            });

            let newHeader = [];
            Header.forEach(item => {
              let obj = {};
              obj.text = item;
              obj.value = item;
              newHeader.push(obj);
            });

            this.headers = newHeader;
            this.tableData = jsonArr;
          };
        }

        this.loading = false;
      } else {
        this.tmpUploadData.data = "";
        this.tableData = [];
      }
    },
    checkFiles() {
      // console.log(this.tmpUploadData.data);
console.log(this.tmpUploadData.data.name)
      if (this.tmpUploadData.data.name.indexOf(".csv") > 0) {
        this.warning.upload = "";
      }

      // if (this.tmpUploadData.data.type == "application/vnd.ms-excel") {
      //   this.warning.upload = "";
      // }
      else {
        this.warning.upload = "只限上傳.csv檔";
      }
      console.log(this.warning.upload)
    },
    uploadData() {
      if (this.tmpUploadData.data != "") {
        // console.log(this.tmpUploadData);
        this.$service.upload.upload(this.tmpUploadData).then(res => {
          // console.log(res);
          if (res) {
            this.controlSnack(
              true,
              "green",
              `成功新增${res.insert}筆資料,更新${res.update}筆資料`
            );
          } else {
            this.controlSnack(true, "red", "檔案格式錯誤");
          }
        });
        // .catch(error => {
        //   console.log(error.response);
        // });
      } else {
        this.controlSnack(true, "red", "請先上傳檔案");
      }
    },
    controlSnack(show, color, text) {
      this.snackbar = show;
      this.snackcolor = color;
      this.text = text;
    }
  },
  mounted() {
  }
};
</script>
